@import url(https://fonts.googleapis.com/css2?family=Dosis&family=Fjalla+One&family=Rubik&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

img {
  display: block;
  width: 100%;
}

h2 {
  margin-bottom: 2.5rem;
}

@media screen and (min-width: 768px) {
  p {
    font-size: 1.2rem;
  }

  h2 {
    font-size: 2rem;
    margin-bottom: 3rem;
  }
  h3 {
    font-size: 1.4rem;
  }
  h4 {
    font-size: 1.2rem;
  }
}

@media screen and (min-width: 1024px) {
  p {
    font-size: 1.2rem;
  }

  h2 {
    font-size: 2.5rem;
    margin-bottom: 3.5rem;
  }
  h3 {
    font-size: 1.8rem;
  }
  h4 {
    font-size: 1.4rem;
  }
}
@media screen and (min-width: 1200px) {
  p {
    font-size: 1.3rem;
  }

  h2 {
    font-size: 2.5rem;
    margin-bottom: 4rem;
  }
  h3 {
    font-size: 1.8rem;
  }
  h4 {
    font-size: 1.4rem;
  }
}

.navbar {
  position: absolute;
  z-index: 2;
  top: 1.5rem;
  right: 0;
}

.menu-bars {
  margin: 2rem 2rem 0 0;
  font-size: 2rem;
  background: none;
  color: white;
}

.nav-menu {
  background-color: #060b26;
  width: 250px;
  height: 100vh;
  display: flex;
  justify-content: center;
  position: fixed;
  top: 0;
  right: -100%;
  transition: 850ms;
  z-index: 3;
  font-family: "Dosis", sans-serif;
  font-weight: bold;
  letter-spacing: 1px;
}

.nav-menu.active {
  right: 0;
  transition: 350ms;
  z-index: 3;
}

.nav-text {
  display: flex;
  align-items: center;
  padding: 8px 0px 8px 16px;
  list-style: none;
  height: 60px;
}

.nav-text a {
  text-decoration: none;
  color: #f5f5f5;
  font-size: 18px;
  width: 95%;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 16px;
  border-radius: 4px;
}

.nav-text a:hover {
  background-color: #1a83ff;
}

.nav-menu-items {
  width: 100%;
  margin-top: 0;
}

.navbar-toggle {
  background-color: #060b26;
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.nav-menu span {
  margin-left: 16px;
}

@media screen and (min-width: 768px) {
  .menu-bars {
    font-size: 3rem;
  }
  .nav-menu-items {
    padding: 0;
  }
  .nav-text {
    margin: 1rem 0;
    padding: 1rem;
  }
  .nav-text a {
    font-size: 27px;
  }
}

.MuiPaper-root.MuiDialog-paper {
  padding: 1rem 2rem;
  font-family: "Dosis", sans-serif;
  background-color: rgb(40, 38, 49);
  color: #bbb;
}

.contct-close-button {
  color: #bbb;
  font-size: 1.5rem;
}

.contact-span {
  font-size: 1.5rem;
  font-weight: 600;
}
.contact-close-contact {
  background: none;
  border: none;
  font-size: 1.5rem;
  cursor: pointer;
}
.contact-close-contact:focus {
  outline: 0;
}
.contact-close-contact {
  color: #bbb;
  font-size: 1.5rem;
}
.contact-form {
  padding: 16;
  padding-top: 0;
}
.contact-labels {
  display: grid;
}

@media screen and (min-width: 1024px) {
  .MuiPaper-root.MuiDialog-paper {
    width: 80vw;
    height: 40vw;
    margin: 0;
  }
  .contct-close-button {
    font-size: 2rem;
  }

  .contact-span {
    font-size: 2rem;
  }

  .contact-close-contact {
    font-size: 2rem;
  }
  .contact-form {
    padding: 16;
    padding-top: 0;
  }
  .contact-labels {
    display: grid;
  }
}

@media screen and (min-width: 1200px) {
  .MuiPaper-root.MuiDialog-paper {
    max-width: 600px;
    max-height: 400px;
    margin: 0;
  }
  .contct-close-button {
    font-size: 2rem;
  }

  .contact-span {
    font-size: 2rem;
  }

  .contact-close-contact {
    font-size: 2rem;
  }
  .contact-form {
    padding: 16;
    padding-top: 0;
  }
  .contact-labels {
    display: grid;
  }
}

.github-calendar {
  display: none;
}

@media screen and (min-width: 768px) {
  .github-calendar {
    display: block;
    margin: 6rem 0;
    font-family: "Dosis", sans-serif;
    font-weight: bold;
  }
  .github-calendar h2 {
    text-align: center;
    color: rgb(190, 190, 190);
    font-size: 1.8rem;
  }

  .github-calendar .calendar {
    width: 90vw;
    max-width: 1300px;
    height: auto;
    border: none;
  }

  .calender .position-relative {
    background-color: black;
  }

  .position-relative .text-gray {
    color: rgb(190, 190, 190);
  }

  .text-gray a {
    color: cornflowerblue;
  }

  .github-calendar .table-column {
    background: black;
    border: none;
  }
  .table-column span {
    color: rgb(190, 190, 190);
    font-family: "Dosis", sans-serif;
    font-weight: bold;
  }
}

@media screen and (min-width: 1024px) {
  .contrib-footer {
    margin-top: 4vw;
    margin-left: 2.34vw;
    margin-right: 2.34vw;
  }
}
@media screen and (min-width: 1200px) {
  .github-calendar .calendar {
    width: 80vw;
    max-width: 1000px;
  }
  .contrib-footer {
    margin-top: 3vw;
    max-width: 950px;
  }
}

.work {
  display: flex;
  flex-flow: column;
  align-items: center;
  margin-bottom: 3rem;
  margin-left: 2rem;
  margin-right: 2rem;
}

.MuiDialog-root[value="work"] .MuiPaper-root.MuiDialog-paper {
  padding: 0;
  height: 80vw;
  max-height: 600px;
  border-radius: 10px;
}

.cards-side-top {
  position: relative;
}

.cards-heading {
  font-weight: 300;
  text-transform: uppercase;
  text-align: right;
  color: white;
  position: absolute;
  top: 0;
  right: 1rem;
  width: 70%;
  padding: 0 0.5rem;
  background-color: #d86121d9;
  font-size: 1.3rem;
  padding: 8px 0.3rem;
}

.cards-back {
  color: white;
  border-radius: 10px;
}
.cards-back p {
  padding: 1rem;
}

.cards-back ul {
  list-style: none;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 0.5rem;
  padding: 0 1rem;
}

.cards-back ul li {
  text-align: center;
  font-size: 0.8rem;
  padding: 0.4rem 0;
  border: 1px solid #bbb;
  border-radius: 5px;
  font-family: "Rubik", sans-serif;
  width: 100%;
}

.detail {
  cursor: pointer;
}

@media screen and (min-width: 768px) {
  .work {
    margin-right: 1rem;
    margin-left: 1rem;
  }

  .MuiDialog-root[value="work"] .MuiPaper-root.MuiDialog-paper {
    height: 60vw;
    max-height: 600px;
  }
  .cards-heading {
    font-size: 1.1rem;
    padding: 0.2rem 0.3rem;
  }
  .cards-back p {
    padding: 3rem;
    font-size: 1.5rem;
  }

  .cards-back ul {
    grid-gap: 0.8rem;
    padding: 0 3rem;
  }

  .cards-back ul li {
    font-size: 1.1rem;
    padding: 0.4rem 0rem;
  }
}
@media screen and (min-width: 1024px) {
  .MuiDialog-root[value="work"] .MuiPaper-root.MuiDialog-paper {
    height: 50vw;
  }
  .cards-heading {
    font-size: 1.3rem;
  }
}
@media screen and (min-width: 1200px) {
  .MuiDialog-root[value="work"] .MuiPaper-root.MuiDialog-paper {
    height: 40vw;
    max-width: 600px;
    max-height: 450px;
  }
}

.works {
  display: flex;
  flex-flow: column;
  align-items: center;
  font-family: "Dosis", sans-serif;
  font-weight: bold;
}

@media screen and (min-width: 768px) {
