.abouts {
  margin-left: 2rem;
  margin-right: 2rem;
  font-family: "Dosis", sans-serif;
  font-weight: bold;
  display: flex;
  flex-flow: column;
  align-items: center;
}

.abouts h3 {
  letter-spacing: 1px;

  margin-top: 3rem;
  margin-bottom: 0rem;
}
.abouts p {
  font-weight: normal;
  margin: 0.5rem 0;
  letter-spacing: 1px;
  line-height: 1.4;
