  }
  .about-me-desc2 {
    grid-column: 1 / -1;
  }
}

@media screen and (min-width: 1500px) {
  .abouts {
    max-width: 1300px;
    margin-left: auto;
    margin-right: auto;
  }

  .descs {
    grid-gap: 1rem 8rem;
    gap: 1rem 8rem;
  }
}

.tech-mobile {
  margin: 3rem 1rem;
  display: flex;
  flex-flow: column;
  align-items: center;
  font-family: "Dosis", sans-serif;
  font-weight: bold;
}
@media screen and (min-width: 768px) {
