#techdesk svg:nth-of-type(1) {
  display: none;
}

.tech-desk {
  display: none;
}

@media screen and (min-width: 768px) {
  .tech-desk {
    display: block;
    margin: 3rem 1rem;
    font-family: "Dosis", sans-serif;
    font-weight: bold;
  }

  .tech-desk h2 {
    text-align: center;
  }

  .tech-desk .buttons {
    display: flex;
    justify-content: center;
  }
  .tech-desk #skill-name {
    display: block;
    font-family: "Dosis", sans-serif;
    font-weight: bold;
    font-size: 1.5rem;
  }

  .button {
    text-align: center;
    border-radius: 8px;
    border: 3px solid rgb(0, 40, 70);
    cursor: pointer;
    margin: 2rem;
    padding: 1rem;
    z-index: 2;
  }

  .button.check {
    -webkit-animation: static-flicker 5s linear infinite;
            animation: static-flicker 5s linear infinite;
  }

  .button:hover {
    -webkit-animation: border-flicker 2s linear forwards;
            animation: border-flicker 2s linear forwards;
  }

  .button span {
    font-family: "Dosis", sans-serif;
    font-weight: bold;
    font-size: 1.5rem;
    margin: 15px 0 0;
    color: rgba(136, 2, 58, 0.767);
  }

  .button:hover span {
    -webkit-animation: text-flicker 2s linear forwards;
            animation: text-flicker 2s linear forwards;
  }

  .tech-desk #skill-name {
    display: block;
    -webkit-animation: name-flicker 4s linear infinite;
            animation: name-flicker 4s linear infinite;
    transform: rotate(-25deg);
    margin-top: -3rem;
    margin-bottom: 3rem;
    font-size: 2rem;
  }

  .skill:hover {
    cursor: pointer;
  }
}

@media screen and (min-width: 1200px) {
  .tech-desk #skill-name {
    margin-top: -17vw;
    margin-bottom: 24vw;
    font-size: 4vw;
    margin-left: 4vw;
  }
  #techdesk {
    margin-top: -8.6vw;
  }
}

@media screen and (min-width: 1500px) {
  .tech-desk {
    max-width: 1500px;
    margin-left: auto;
    margin-right: auto;
  }
  .tech-desk #skill-name {
    font-size: 3rem;
  }

