@-webkit-keyframes static-flicker {
  2% {
    border: 3px solid rgb(0, 40, 70);
    box-shadow: none;
  }
  20% {
    border: 3px solid rgb(230, 0, 120);
    box-shadow: 0 0 15px -1px rgb(230, 0, 120),
      0 0 12px -1px rgb(230, 0, 120) inset;
  }
  21% {
    border: 3px solid rgb(0, 40, 70);
    box-shadow: none;
  }
  22% {
    border: 3px solid rgb(230, 0, 120);
    box-shadow: 0 0 15px -1px rgb(230, 0, 120),
      0 0 12px -1px rgb(230, 0, 120) inset;
  }
  23% {
    border: 3px solid rgb(0, 40, 70);
    box-shadow: none;
  }
  60% {
    border: 3px solid rgb(230, 0, 120);
    box-shadow: 0 0 15px -1px rgb(230, 0, 120),
      0 0 12px -1px rgb(230, 0, 120) inset;
  }
  61% {
    border: 3px solid rgb(0, 40, 70);
    box-shadow: none;
  }
}

